.active {
    position: relative;
    bottom: 2px;
    font-size: 20px;
    color: black;
}

.pagination {
    padding: 5px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    position: relative;
    color: white;
    font-weight: bold;
    font-size: 1em;
}

.pagination span {
    padding: 5px;
    transition: 0.2s all ease-in-out;
    /*position: relative;*/
}

.pagination span:hover {
    color: gold;
    cursor: pointer;
    transform: scale3d(1.5,1.5,2);
    /*box-sizing: border-box;*/
}

.pagination span:active {
    font-size: 18px;
    color: red;
}

.changeButton {
    width: 3em;
    height: 3em;
    /*padding: 3px 3px 3px 3px;*/
    /*margin-top: 3px;*/
    border-radius: 50%;
    /*background-color: teal;*/
    color: white;
}
.pageSummary{
    font-size: 12px;
}
